<template>
  <div>
    <b-container>
      <h1>Edit person</h1>
      <b-form @submit="onSubmit">
        <b-form-group
            id="input-group-nickname"
            label="Nickname:"
            label-for="input-nickname"
            description="The usual names you are called"
        >
          <b-form-input
              id="input-nickname"
              v-model="person.nickname"
              type="text"
              placeholder="Enter nickname"
              required
          ></b-form-input>
        </b-form-group>
        <b-form-group
            id="input-group-firstName"
            label="First Name:"
            label-for="input-firstName"
            description="The first name"
        >
          <b-form-input
              id="input-firstName"
              v-model="person.firstName"
              type="text"
              placeholder="Enter first name"
              required
          ></b-form-input>
        </b-form-group>
        <b-form-group
            id="input-group-lastName"
            label="Last Name:"
            label-for="input-lastName"
            description="Your last name"
        >
          <b-form-input
              id="input-lastName"
              v-model="person.lastName"
              type="text"
              placeholder="Enter last name"
              required
          ></b-form-input>
        </b-form-group>
        <b-form-group
            id="input-group-height"
            label="Height:"
            label-for="input-height"
            description="Enter your height"
        >
          <b-form-input
              id="input-height"
              v-model="person.height"
              type="number"
              placeholder="Enter height"
              required
          ></b-form-input>
        </b-form-group>
        <b-button type="submit" variant="primary">Edit</b-button>
      </b-form>
    </b-container>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "EditPerson",
  data(){
    return{
      person:{

      }
    }
  },
  created() {
    this.getPerson();
  },
  methods:{
    getPerson(){
      axios.get("/persons/"+this.$route.params.id)
      .then((response)=> {
        this.person = response.data;
      });
    },

    onSubmit(event) {
      event.preventDefault()
      axios.put("/persons",this.person)
          .then((res)=>{
            console.log(res)
            //happy case
          })
          .catch((error)=>{
            console.log("error: " + error)
            //error.response.status, show error message
          })
          .finally(()=>{
            //performed in all cases
          })
    }
  }
}
</script>

