<template>
<div>
  <b-container>
    <h1>List of persons</h1>
    <b-table striped hover :items="persons" :fields="fields">
      <template #cell(actions)="row">
        <b-button @click="deleteItem(row.item.idPerson)">Delete</b-button>
        <b-button @click="editItem(row.item.idPerson)">Edit</b-button>
      </template>
    </b-table>

  </b-container>
</div>
</template>

<script>
import axios from 'axios';
export default {
name: "Persons.vue",
  data(){
    return {persons:[], errors: [],
    fields:[
      {key:"idPerson"},
      {key:"nickname"},
      {key:"firstName"},
      {key:"lastName"},
      {key:"birthDay"},
      {key:"height"},
      {key:'gender'},
      {key:"actions"}
    ]}
  },
  created() {
    axios.get("/persons")
    .then(response => this.persons = response.data )
    .catch(e => (this.errors.push(e)))
  },
  methods:{
  deleteItem(id){
    axios.delete("/persons/"+id)
    .then(response=>{
      console.log(response);
      const index =this.persons.findIndex(person=>person.idPerson ==id);
      if (~index){
        this.persons.splice(index,1);
      }
    })
  },
  editItem(id){
    this.$router.push({path:"editperson/"+id})
  }

  }
}
</script>

